import React, {useCallback, useEffect, useState} from "react"
import { Link, graphql } from "gatsby"
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"

const ALL_NAME = 'ALL'
const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const allPosts = data.allMarkdownRemark.nodes
  const categories = [{fieldValue: ALL_NAME, totalCount: allPosts.length}, ...data.categories.group]

  const [posts, setPosts] = useState([...allPosts]);
  const [curCategory, setCurCategory] = useState(ALL_NAME);
  const [keyword, setKeyword] = useState('');

  const onChangeKeyword = useCallback((e) => {
    setKeyword(e.target.value);
  }, []);

  const onClickCategory = useCallback((e) => {
    setCurCategory(e.target.innerText);
  }, []);

  const searchProc = useCallback((e) => {
    e.preventDefault();
    if(!keyword || !keyword.trim()) {
      setCurCategory(ALL_NAME);
      setPosts([...allPosts]);
      return;
    }
    setPosts([...allPosts.filter((post)=> {
      if ((post.frontmatter.title?.includes(keyword) || post.frontmatter.description?.includes(keyword))) {
        if (curCategory !== ALL_NAME) 
          return post.frontmatter.category === curCategory;
        return true;
      }
  })])
  }, [keyword, curCategory]);

  useEffect(() => {
    if (curCategory === ALL_NAME)
      setPosts([...allPosts])
    else
      setPosts([...allPosts.filter((post)=>post.frontmatter.category === curCategory)])
  }, [curCategory]);

  return (
    <Layout location={location} title={siteTitle}>
      <Bio />
      <div className="tag-wrapper">
        {categories.map(category => (
          <div
            className={curCategory===category.fieldValue ? "tag-button selected": "tag-button"}
            key={category.fieldValue}
            onClick={onClickCategory}
          >
            {category.fieldValue}
          </div>
        ))}
      </div>
      <h4>{curCategory}</h4>
      <div className="post-list-header">
        <span>{posts.length} 개의 글</span>
        <form className="search-input" onSubmit={searchProc}>
          <input type="text" value={keyword} onChange={onChangeKeyword}/>
          <FontAwesomeIcon className="search-icon" icon={faSearch}/>
        </form>
      </div>
      <div className="post-wrapper" style={{ listStyle: `none` }}>
        {posts.length === 0 && <div className="center-pos">포스트가 존재하지 않습니다.</div>}
        {posts.map(post => {
          const title = post.frontmatter.title || post.fields.slug
          return (
            <div className="post-item" key={post.fields.slug}>
              <Link to={post.fields.slug} itemProp="url">
                <article
                  className="post-list-item"
                  itemScope
                  itemType="http://schema.org/Article"
                >
                  <header>
                    <h4>
                      <span itemProp="headline">
                        {title} {post.frontmatter.icon}
                      </span>
                    </h4>
                    <div className="flex-wrapper">
                      <span className="post-tag">
                        {post.frontmatter.category}
                      </span>
                      <small>{post.frontmatter.date}</small>
                    </div>
                  </header>
                  <section>
                    <p>{post.frontmatter.description || post.excerpt}</p>
                  </section>
                </article>
              </Link>
            </div>
          )
        })}
      </div>
    </Layout>
  )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Huiy's Blog" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
      nodes {
        excerpt(pruneLength: 100)
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          category
          icon
        }
      }
    }
    categories: allMarkdownRemark(limit: 2000) {
      group(field: frontmatter___category) {
        fieldValue
        totalCount
      }
    }
  }
`